import React, { useState } from "react";
import WomenPhrases from "./WomenPhrases";
import womenInfo from "../Data/womenInfo";
import "./WomenVoices.css";
function WomenVoices() {
    
  const [woman, setWoman] = useState("diana-sacayan");

  return (
    <div>
      {/* Filtros de mujeres */}
      <div className="women-voices-container">
        {womenInfo.map(({ key, name, image }) => {
          return (
            <div
              class="flip-card"
              onClick={() => {
                setWoman(key);
              }}
            >
              <div class="flip-card-inner woman-container">
                <div class="flip-card-front image-container">
                  <img src={image} alt={name} className="woman-image" />
                </div>
                <div class="flip-card-back">
                  <span>{name}</span>
                </div>
              </div>
            </div>
          );
        })}
      </div>

      {/* Frases de mujeres */}
      <WomenPhrases chosenWoman={woman} />
    </div>
  );
}

export default WomenVoices;
