import React from "react";
import womenInfo from "../Data/womenInfo";
import "./WomenPhrases.css";
import leftQuotesSign from "./left-quotes-sign.svg";

function WomenPhrases({ chosenWoman }) {
  const woman = womenInfo.filter((woman) => woman.key === chosenWoman)[0];

  return (
    <div className="main-container">
      <div className='image-name-container'> 
         <div className='woman-image-container'>
        <img className='woman-image-phrase' src={woman.image} alt={woman.name}/>
      </ div>
      <span className='woman-name-phrase'>{woman.name}</span>
      </div>
     
      
      <div className ="phrase-container">
        {/* <img
          src={leftQuotesSign}
          alt="Left quotes sign"
          className="left-quotes-sign"
        /> */}

        <span>{woman.phrase}</span>
      </div>

      <span className="data">{woman.data}</span>
    </div>
  );
}
export default WomenPhrases;
